import cTitle from 'components/title';
import { mapState,mapMutations } from 'vuex';
 import { MessageBox } from 'mint-ui';
export default
  {
    data() {
      return {
        balance: 0,
        payParams: '',
        info_form: {},
        order_sn:'',
        money:''
      }
    },
    methods:
    {
      confirm(type)
      {
        //type-1 微信, type-2 支付宝
        if(type == 1)
        {
          this.getWeChatPayParams();

        }else
        {


        }

      },
       getOrderInfo() {
        //order.pay.wechatPay
        var that = this;
        $http.get('order.pay',{order_id:this.$route.params.order_id}).then(function (response) {
          if (response.result == 1) {
           console.log(response.data);
           that.order_sn = response.data.order.order_sn;
          that.balance = response.data.member.credit2;
          that.money = response.data.order.price;
          } else {
            console.log(response.msg);

          }
        }, function (response) {
          // error callback
        });
      },
      getWeChatPayParams() {
        //order.pay.wechatPay
        var that = this;
        $http.get('order.pay.wechatPay',{order_id:this.$route.params.order_id}).then(function (response) {
          if (response.result == 1) {
           
           wx.config(response.data.js);

           that.WXPay(response.data.config);
       
          } else {
         
        MessageBox.alert(response.msg,'提示');
          }
        }, function (response) {
          // error callback
        });
      },
      initHtml(){
        console.log(this.$store.state.balance);
        this.balance = this.$store.state.balance;

      },
      WXPay(payParams) {
       //alert(document.location.href);
       //console.log(""+payParams.timestamp);
        var that = this;
        wx.chooseWXPay({
          appId:payParams.appId,
          timestamp:payParams.timestamp , // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
          nonceStr:payParams.nonceStr, // 支付签名随机串，不长于 32 位
          package:payParams.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
          signType:payParams.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
          paySign:payParams.paySign, // 支付签名
          success: function (res) {
            // 支付成功后的回调函数
            if (res.errMsg == "chooseWXPay:ok") {
              that.$router.go(-1);
            } else {
             
              alert('支付失败');
            }
          },
          cancel: function (res) {
            //支付取消
          },
          fail: function (res) {
            
            alert("支付失败，请返回重试。");
             //MessageBox.alert('支付失败，请返回重试','提示');
          }
        });


      }
    },
    activated () {
      
      //this.order_sn = this.$route.params.order.order_sn;
      //this.money = this.$route.params.order.price;
      this.getOrderInfo();
      //this.getWeChatPayParams();
      this.initHtml();
    },
    components: { cTitle }
  }   


