		</div>
		<!--<div class="mod_btns" id="alipay">
			<a @click = "confirm(2)" class="mod_btn bg_ali">支付宝支付</a>
		</div>-->
	</div>
		<!--<mt-button type="primary"
		           size="large"
		           @click="getWeChatPayParams">微信支付</mt-button>-->
	</div>
